import Vue from 'vue'
import App from './App.vue'


import Buefy from 'buefy'
import 'buefy/dist/buefy.css'

import router from './router'
import store from './store'

import axios from 'axios'

Vue.prototype.$axios = axios
Vue.config.productionTip = false
Vue.use(Buefy)

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
